import Vue from 'vue'
import VueI18n from 'vue-i18n'

Vue.use(VueI18n)

const setDateTimeFormats = {
  long: {
    year: "numeric",
    month: "long",
    day: "numeric"
  }
}

const dateTimeFormats = {
  "ar": setDateTimeFormats,
  "en": setDateTimeFormats,
  "es": setDateTimeFormats,
  "he": setDateTimeFormats,
  "id": setDateTimeFormats,
  "ro": setDateTimeFormats,
  "th": setDateTimeFormats,
  "tr": setDateTimeFormats,
  "zh-hk": setDateTimeFormats,
  "zh-tw": setDateTimeFormats,
}

function loadLocaleMessages () {
  const locales = require.context('./locales', true, /[A-Za-z0-9-_,\s]+\.json$/i)
  const messages = {}
  locales.keys().forEach(key => {
    const matched = key.match(/([A-Za-z0-9-_]+)\./i)
    if (matched && matched.length > 1) {
      const locale = matched[1]
      messages[locale] = locales(key)
    }
  })
  return messages
}

export default new VueI18n({
  locale: process.env.VUE_APP_I18N_LOCALE || 'zh',
  fallbackLocale: process.env.VUE_APP_I18N_FALLBACK_LOCALE || 'en',
  messages: loadLocaleMessages(),
  silentFallbackWarn: true,
  dateTimeFormats
})
